
import XDialog from 'vux/src/components/x-dialog/index.vue'

import levels from '@/assets/level/level-show'
export default {
data () {
return {
powsers: ['贵族身份头衔；', '昵称加贵族称呼；', '更多特权暂未开发，敬请期待！'],
curLevelIcon: '',
levelVisible: false,
levels: levels
}
},
methods: {
showLevel (icon) {
this.curLevelIcon = icon
this.levelVisible = true
}
},
components: {
XDialog
}
}



